.checkbox {
  font-weight: 600;
  font-size: 1.25rem;
  line-height: 1.08;
  color: #2443BE;
}

.checkbox a {
  color: #E53626;
}

.checkbox input {
  display: none;
}

.checkbox > span {
  padding-left: 2.625rem;
  position: relative;
  display: inline-block;
}

.checkbox > span:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0px;
  transform: translateY(-50%);
  display: inline-block;
  border: 2px solid #2443BE;
  border-radius: 5px;
  background-color: transparent;
  width: 1.875rem;
  height: 1.875rem;
  transition: 0.3s;
  margin-top: -3px;
}

.checkbox > span:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0px;
  transform: translateY(-50%) translateY(5px) scale(0.9);
  display: inline-block;
  border-radius: 5px;
  background-color: transparent;
  width: 1.875rem;
  height: 1.875rem;
  transition: 0.3s;
  margin-top: -3px;
  background-image: url("../img/general/check.svg");
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0;
}

.checkbox input:checked + span:after {
  transform: translateY(-50%) translateY(0px) scale(0.9);
  opacity: 1;
}

.checkbox_white {
  color: #fff;
}

.checkbox_white > span:before {
  border-color: #fff;
}

.checkbox_white > span:after {
  background-image: url("../img/general/check-white.svg");
}

.checkbox_light-text {
  font-size: 14px;
  color: #272727;
  font-weight: 400;
}

.checkbox_light-text a {
  text-decoration: underline;
  color: #2443BE;
}

.radio {
  font-weight: 600;
  font-size: 1.25rem;
  line-height: 1.08;
  color: #2443BE;
}

.radio a {
  color: #E53626;
}

.radio input {
  display: none;
}

.radio > span {
  padding-left: 2.625rem;
  position: relative;
  display: inline-block;
}

.radio > span:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0px;
  transform: translateY(-50%);
  display: inline-block;
  border: 2px solid #2443BE;
  border-radius: 50%;
  background-color: transparent;
  width: 1.875rem;
  height: 1.875rem;
  transition: 0.3s;
  margin-top: -3px;
}

.radio > span:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0px;
  transform: translateY(-50%) scale(0.1);
  display: inline-block;
  border-radius: 50%;
  width: 1.875rem;
  height: 1.875rem;
  transition: 0.3s;
  margin-top: -3px;
  background: #2443BE;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0;
}

.radio input:checked + span:after {
  transform: translateY(-50%) scale(0.6);
  opacity: 1;
}

html body .bvi-body .checkbox input {
  display: inline-block;
}

html body .bvi-body .checkbox > span:before,
html body .bvi-body .checkbox > span:after {
