@media (max-width: 991.98px) {
  .checkbox {
    display: flex;
    align-items: center;
  }
  .checkbox > span {
    padding-left: 50px !important;
    padding-top: 0;
  }
  .checkbox > span:before,
  .checkbox > span:after {
    width: 30px;
    height: 30px;
    margin-top: 0;
  }
  .checkbox {
    min-height: 30px;
    display: block;
  }
  .checkbox input:checked + span:after {
    transform: translateY(-50%) scale(0.5);
  }
}

