body {
  color: #171717;
  margin: 0;
  font-family: "Montserrat", Arial, sans-serif;
  font-size: 16px;
  line-height: 1.2;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow-x: hidden;
  opacity: 1;
  transition: opacity 0.5s;
  overflow-y: scroll;
  background: white;
}

@supports (animation-name: load) {
  body body {
    opacity: 0;
    transition: opacity 0.4s;
  }
  body body:not(.body-ready) {
    animation: load;
    animation-duration: 0.2s;
    animation-delay: 5s;
    animation-fill-mode: forwards;
  }
}

@keyframes load {
  /* @supports being a CSS conditional group at-rule, it can includes other relevant at-rules */
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

body:not(.body-ready) {
  opacity: 0;
  background: #fff;
  animation-name: body-load;
  animation-delay: 5s;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

body.inner-light {
  background: #fff;
}

body.menu-open:before {
  position: fixed;
  z-index: 40;
  opacity: 1;
  pointer-events: all;
}

@media (max-width: 991.98px) {
  body.menu-open:before {
    display: none;
  }
}

body:before {
  content: '';
  background: rgba(0, 0, 0, 0.43);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  pointer-events: none;
  opacity: 0;
}

.main {
  display: block;
