.page-header_abs {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.page-header__row {
  position: relative;
  z-index: 15;
  display: flex;
  align-items: center;
  margin: 0 -10px;
  max-height: 148px;
}

.page-header__col-left {
  flex: 0 0 40%;
  padding: 0 10px;
  display: flex;
  align-items: center;
  margin-top: 0.8125rem;
}

.page-header__col-logo {
  flex: 0 0 20%;
  padding: 0 10px;
}

.page-header__col-right {
  flex: 0 0 40%;
  padding: 0 10px;
  display: flex;
  align-items: center;
  margin-top: 0.8125rem;
}

.page-header__glass {
  margin-right: 1.875rem;
}

.page-header__search {
  margin-right: auto;
  width: 90px;
  padding-right: 10px;
  flex-shrink: 0;
  height: 2.25rem;
  position: relative;
}

.page-header__search_open .page-header__search-control {
  max-width: 300px;
  opacity: 1;
}

.page-header__logo-small {
  display: none !important;
}

.page-header__search-form {
  background: #2226a3;
  border-radius: 1.25rem;
  padding: 0.5625rem 1.25rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
}

.page-header__search-control {
  background: transparent;
  border: none;
  border-bottom: 1px solid white;
  color: #fff;
  max-width: 0;
  opacity: 0;
  transform-origin: left;
  transition: 0.3s max-width;
  border-radius: 0 !important;
}

.page-header__search-cont {
  display: flex;
  align-items: center;
}

.page-header__search-btn {
  background: transparent;
  display: flex;
  align-items: center;
}

.page-header__search-btn svg {
  width: 1.1875rem;
  height: 1.1875rem;
}

.page-header__menu {
  margin: 0 -0.9375rem;
}

.page-header__menu-link {
  display: inline-block;
  vertical-align: top;
  padding: 10px 0.9375rem;
  font-weight: 600;
  font-size: 14px;
  color: #fff;
}

.page-header__menu-link:hover {
  text-shadow: 3px 3px 20px #FF00D6;
}

.page-header__menu-link:active {
  text-shadow: 20px 10px 5px #FF00D6;
}

.page-header__logo {
  display: block;
  text-align: center;
  margin-bottom: -3.125rem;
}

