}

.user-content h1, .user-content .h1 {
  font-family: "Open Sans", Arial, sans-serif;
  color: inherit;
  font-size: 2.5rem;
}

.user-content h2, .user-content .h2 {
  font-family: "Open Sans", Arial, sans-serif;
  color: inherit;
  font-size: 2.25rem;
}

.user-content h3, .user-content .h3 {
  font-family: "Open Sans", Arial, sans-serif;
  color: inherit;
  font-size: 2rem;
}

.user-content h4, .user-content .h4 {
  font-family: "Open Sans", Arial, sans-serif;
  color: inherit;
  font-size: 1.75rem;
}

.user-content h5, .user-content .h5 {
  font-family: "Open Sans", Arial, sans-serif;
  color: inherit;
  font-size: 1.5rem;
}

.user-content h6, .user-content .h6 {
  font-family: "Open Sans", Arial, sans-serif;
  color: inherit;
  font-size: 1.25rem;
}

.user-content ul {
  margin: 0;
  padding: 0;
}

.user-content ul li {
  font-weight: 400;
  display: inline-flex;
  width: 100%;
  margin-bottom: 4px;
  position: relative;
  padding-left: 15px;
}

.user-content ul li:before {
  content: '';
  display: inline-block;
  width: 0.5625rem;
  height: 0.125rem;
  border-radius: 100%;
  background: #0f1966;
  margin-right: 0.3125rem;
  flex-shrink: 0;
  position: absolute;
  top: 0.8em;
  left: 0;
}

.user-content ol {
  padding-left: 13px;
}

.user-content ol li {
  margin-bottom: 9px;
}

.user-content q::before {
  display: none;
}

.user-content q::after {
  display: none;
}

.user-content q, .user-content blockquote {
  display: block;
  max-width: 975px;
  border-left: 0.1875rem solid #0f1966;
  padding: 0.875rem 1.25rem;
  margin-left: 1.875rem;
}

.user-content table {
  border-collapse: collapse;
  display: block;
  max-width: 100%;
  overflow-x: auto;
}

.user-content table th, .user-content table td {
