.gamburger {
  font-size: 10px;
  display: inline-block;
  width: 2.8125rem;
  height: 16px;
  transition: 1s;
  position: relative;
  cursor: pointer;
}

@media (max-width: 767.98px) {
  .gamburger {
    font-size: 7px;
  }
}

.gamburger span {
  display: block;
  width: 100%;
  height: 2px;
  background: #fff;
  position: absolute;
}

.gamburger-line1 {
  top: 0;
  left: 0;
  transition: 0.4s;
}

.gamburger-line2 {
  top: 50%;
  margin-top: -1px;
  left: 0;
}

.gamburger-line3 {
  top: 100%;
  margin-top: -2px;
  left: 0;
  transition: 0.4s;
}

.gamburger.open .gamburger-line1 {
  transform: rotate(45deg);
  top: 50%;
  margin-top: -1px;
}

.gamburger.open .gamburger-line2 {
  position: absolute;
  right: -400vw;
  left: auto;
}

.gamburger.open .gamburger-line3 {
  transform: rotate(-45deg);
  top: 50%;
  margin-top: -1px;
  transform-origin: center;
}

.gamburger.open span {
  background: #fff;
}
